import React from 'react';

const InstagramPost = ({ post }) => {
    // –û–ø—Ä–µ–¥–µ–ª—è–µ–º —Ñ–æ—Ä–º–∞—Ç –ø–æ—Å—Ç–∞ –Ω–∞ –æ—Å–Ω–æ–≤–µ —Ç–∏–ø–∞
    const isReel = post.type === 'reel';
  
    return (
      <div 
        className="post-card"
        style={{
          // –î–ª—è Reels –∏—Å–ø–æ–ª—å–∑—É–µ–º —Å–æ–æ—Ç–Ω–æ—à–µ–Ω–∏–µ 9:16, –¥–ª—è –æ–±—ã—á–Ω—ã—Ö –ø–æ—Å—Ç–æ–≤ - 1:1
          aspectRatio: isReel ? '9/16' : '1/1',
          position: 'relative',
          backgroundColor: '#f1f1f1',
          overflow: 'hidden',
          width: '100%'
        }}
      >
        {post.imageUrl ? (
          <img
            src={post.imageUrl}
            alt={`–ü–æ—Å—Ç ${post.title}`}
            style={{
              width: '100%',
              height: '100%',
              objectFit: 'cover'
            }}
          />
        ) : (
          <div
            style={{
              width: '100%',
              height: '100%',
              backgroundColor: '#f1f1f1'
            }}
          />
        )}
      </div>
    );
  };
  
  export default InstagramPost;